body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 700;
}

.header {
  background-color: fff;
  justify-content: space-between;
  width: 1200px;
  display: flex;

  & .img {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 50px;
    display: block;
  }

  & .nav {
    justify-content: center;
    align-items: center;
    margin-right: 50px;
    display: flex;
  }

  & a {
    color: #000;
    text-decoration: none;
  }

  & ul {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    list-style: none;
    display: flex;
  }

  & li:not(:last-child) {
    margin-right: 20px;
  }
}

.blue, a:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: b#00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: -22.5px;
  left: 0;
}
